.card{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: rgb(82, 56, 56);

  margin: 30px auto;
  width: 250px;
  height: 250px; 
  border-radius: 20px;
  box-shadow: 2px 0px 5px 2px rgba(0,0,0,0.25), -5px 0px 5px 0px rgba(0,0,0,0.22);
  cursor: pointer;
  transition: 0.4s;
  color: rgb(210, 210, 210);
}

.card:hover {
  transform: scale(1.2, 1.2);
  box-shadow: 2px 2px 10px 5px rgba(0,0,0,0.25), 
    -5px -5px 10px 0px rgba(0,0,0,0.22);
}

.cards{
  display: flex;
  flex-wrap: wrap;
}

.name{
  text-align: center;
  font-size: 20px;
  font-weight: bold;
}

.marks{
  text-align: center;
  font-size: 30px;
}

.grade{
  height: 5px;
}

.color{
  text-align: center;
}

.rating{
  height: 15%;
  display: flex;
  flex-direction: column;
  justify-content: space-between
}

.A{
  background-color: #00FF00;
}

.B{
  background-color: #CCFF00;
}

.C{
  background-color: #FFCC33;
}

.D{
  background-color: #FF9900;
}

.E{
  background-color: #FF3366;
}

.F{
  background-color: #FF0000;
}